@import "./base/colors";
$dummy: "../../assets/images/hamster.gif";
.categories {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
}

.category {
    transform: perspective(300px) scale(1.05) rotateX(10deg);
    margin-top: 25px;
    width: 200px;
    height: 200px;
    background: url($dummy) center no-repeat;
    background-color: $bgcolor;
    background-size: cover;
    color: $accentColor;
    display: grid;
    place-content: center;
    place-items: center;
    position: relative;
    box-shadow: 3px 7px 5px rgba(0, 0, 0, 0.5), 3px 3px 7px rgba(0, 0, 0, 0.5) inset;
    transition: all .2s ease-in-out;
    &:hover {
        transform: perspective(0px) scale(1.1) rotateX(25deg);
        &>.category-name {
            top: -15px;
            height: 50px;
        }
    }
