        background-size: cover;
        color: $secondColor;
        padding: 10px;
        width: 100%;
        font-size: 17px;
        font-weight: bold;
        text-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.5);
        text-align: center;
        box-sizing: border-box;
